import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import RatingsHeader from './components/ratingsHeader'
import TopCoinCard from './components/topCoinCard'
import TableRow from './components/tableRow'
import TableHeader from './components/tableHeader'
import RatingsFooter from './components/ratingsFooter'
import Bar from './components/bar'

const RatingsPage = ({
  topCoins,
  valuation,
  colors,
  bitcoinPredict,
  restCoins,
  confidence,
  date,
  loaded,
}) => (
  <div className={classnames('rating-page', { loaded })}>
    <div className="content-wrapper">
      <RatingsHeader confidence={confidence} date={date} />
      <div className="cards-section">
        {topCoins.map((coin, index) => (
          <TopCoinCard key={coin.coinName} {...coin} rank={index + 1} />
        ))}
      </div>
      <div className="table-section">
        <div className="scroll-wrapper">
          <TableHeader />
          {restCoins.map((coin, index) => (
            <TableRow
              key={coin.coinName}
              {...coin}
              isEven={index % 2 === 0}
              rank={index + 4}
            />
          ))}
        </div>
      </div>
      <div className="flex predict-section">
        <div className="rounded predict-card left">
          <div className="header">
            <span>“In one year from now, the Crypto market will go…”</span>
          </div>
          <div className="content">
            {valuation.map((item, index) => (
              <div key={index} className="flex line">
                <span className="predict-label">{item.label}</span>
                <div className="line-bar narrow">
                  <Bar wide percentage={item.value} barColor={colors[index]} />
                  <span className="digit">{item.value.toFixed(0)}%</span>
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className="rounded predict-card right">
          <div className="header">
            <span className="column-headline">Bitcoin Will Be Worth...</span>
            <span className="column-title">IN 1 YEAR</span>
            <span className="column-title">IN 5 YEARS</span>
          </div>
          <div className="content">
            {bitcoinPredict.map((item, index) => (
              <div key={index} className="flex line">
                <span className="flex btc-label">{item.label}</span>
                <div className="line-bar">
                  <Bar
                    wide
                    noMargin
                    percentage={item.value1}
                    barColor={colors[index]}
                  />
                  <span className="digit">{item.value1.toFixed(0)}%</span>
                </div>
                <div className="line-bar">
                  <Bar
                    wide
                    noMargin
                    percentage={item.value2}
                    barColor={colors[index]}
                  />
                  <span className="digit">{item.value2.toFixed(0)}%</span>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
      <RatingsFooter />
    </div>
  </div>
)

RatingsPage.propTypes = {
  topCoins: PropTypes.any,
  valuation: PropTypes.any,
  colors: PropTypes.any,
  bitcoinPredict: PropTypes.any,
  restCoins: PropTypes.any,
  confidence: PropTypes.any,
  date: PropTypes.any,
  loaded: PropTypes.bool,
}

export default RatingsPage
