import React from 'react'
import { BrowserRouter as Router } from 'react-router-dom'
import { Switch, Route } from 'react-router'
import CoinRatings from './coin-ratings'
import CommonRatings from './common-ratings'

const AppRouter = () => (
  <Router>
    <Switch>
      <Route path="/" exact component={CommonRatings} />
      <Route path="/ratings/" component={CommonRatings} />
      <Route path="/ratings/BTC" component={CoinRatings} />
    </Switch>
  </Router>
)

export default AppRouter
